.recipes {
  width: 100vw;
  height: 100vh;
  z-index: 20;
  -webkit-transition:top 1.5s ease-in-out;
  -o-transition:top 1.5s ease-in-out;
  transition:top 1.5s ease-in-out;
  background: white;
  top: 100%;
  position: absolute;
  overflow-y: auto;
  overflow-x: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.recipes.visible {
  top: 0;
}

.recipe {
  max-width: 500px;
}
.recipe .image {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    overflow: hidden;
    max-height: 300px;
}
.recipe .image img {
    max-width: 100%;
    height: auto;
    width: auto;
}

.recipe .container {
  position: absolute;
  top: 0px;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(10%, rgba(0,0,0,1)), color-stop(30%, rgba(0,0,0,0)));
  background: -webkit-linear-gradient(top, rgba(0,0,0,1) 10%, rgba(0,0,0,0) 30%);
  background: -o-linear-gradient(top, rgba(0,0,0,1) 10%, rgba(0,0,0,0) 30%);
  background: linear-gradient(to bottom, rgba(0,0,0,1) 10%, rgba(0,0,0,0) 30%);
  color: white;
  text-align: left;
  width: 100%;
}

.recipe .container div {
  margin: 5px 10px;
}



